import refresh from '../../../components/refresh.vue';

export default {
	components: { refresh },
	data() {
		return {
			hasMsg: false,
			serchPara: '',
			listItem: [
			]
		};
	},
	onLoad: function(e) {
		var msg = e.msg;
		console.log('输如了' + msg);
		this.serchPara = e.msg;
		
		
		this.serchMethod();
	},
	methods: {
		back() {
			uni.navigateBack();
		},
		//搜索
		confirm(e) {
			console.log('输入了' + e.detail.value);
			this.serchPara = e.detail.value.trim();
			//收起键盘

			uni.hideKeyboard();




			if(e.detail.value.trim().length == 0){
				return;
			}
			this.serchMethod();
		},
		tabSelect(e) {
			uni.navigateTo({
				url: '/pages/videoPage/videoPlayer/videoPlayer?video=' + JSON.stringify(e)
			});
		},
		serchMethod(){
			//跳转到搜索页
			
			uni.showLoading({
				title: '加载中',
				mask: true
			});
			
			uni.request({
				url: this.$serverUrl + '/getSearch',
				data: this.serchPara,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: ret => {
					if (ret.statusCode !== 200) {
						console.log('请求主题信息失败:', ret);
						this.hasMsg = true;
						//隐藏加载loading
						uni.hideLoading();
					}else{
						uni.hideLoading();
						var videos = ret.data;
						var thisPageVideos = [];
						for (var j = 0; j < videos.length; j++) {
							var video = videos[j];
							//解决显示必须一致的问题
						
							var sc = video.showScore;
							if (sc.length == 1) {
								sc = sc + '.0';
							} else if (sc.length == 0) {
								sc = '0.0';
							} else if (sc.length == 4) {
								sc = sc.slice(0, 2);
							}
						
							var info = video.newInfo;
							if (info.length == 0) {
								info = 'HD高清';
							}
						
							var na = video.name;
							if (na.length == 0) {
								na = '无';
							}
						
							var videoTrans = {
								id: video.id,
								name: na,
								new_info: info,
								show_score: sc,
								//picture: this.$serverUrl + '/images/' + video.picture,
								//暂时用其他服务器的,感觉没必要用自己的
								
								picture: video.pictureUrl,
								director: video.director,
								actors: video.actors,
								release_time: video.releaseTime,
								updated_time: video.updatedTime,
								area: video.area,
								language: video.language,
								film_length: video.filmLength,
								film_synopsis: video.filmSynopsis.trim(),
								show_url: video.lookUrl,
								down_url: video.downUrl
							};
						
							//当页的视频集合
							thisPageVideos.push(videoTrans);
						}
						this.listItem =thisPageVideos;
						if(thisPageVideos.length > 0){
							this.hasMsg = false;
						}else{
							this.hasMsg = true;
						}
						console.log(ret);
						this.$forceUpdate();
					};
					//隐藏加载loading
					uni.hideLoading();
				},
				fail: (e) => {
					//隐藏加载loading
					uni.hideLoading();
				}
			});
		}
	}
};
